<script>
	export let article;
	export let dim;
	export let onClick;

	const image = article.summary ? article.summary.lead_image_url : null;
	const description = article.summary ? article.summary.excerpt : article.description;
	const readingTime = article.summary ? `${article.summary.readingTime} minute read` : 'unknown';
	const widthClass = image ? 'w-60-ns' : 'w-100-ns';
</script>

<style>
article {
	cursor: pointer;
}
.dimNow { opacity: 0.5 }
</style>

<article class="bt bb b--black-10" class:dimNow={dim}>
	<div class="db pv4 ph3 ph0-l no-underline dim" on:click|preventDefault={onClick}>
		<div class="flex flex-column flex-row-ns">
			{#if image}
				<div class="pr3-ns mb4 mb0-ns w-100 w-40-ns">
					<img src={image} class="db" alt="Photo of a dimly lit room with a computer interface terminal.">
				</div>
			{/if}
			<div class="{`w-100 ${widthClass} pl3-ns`}">
				<h1 class="f3 fw1 baskerville mt0 lh-title">{article.title}</h1>
				<p class="f6 f5-l lh-copy">
					{description}
				</p>
				<p class="f6 lh-copy mv0">{readingTime}</p>
			</div>
		</div>
	</div>
</article>
