<script>
	export let feed;
	export let toggleArticle;
	import ArticlePreview from './ArticlePreview.svelte';
	import { loadSummaries } from './rss_fetch';
	import { queue } from './stores';

	let articlesPromise = loadSummaries(feed);
	let feedOpen = false;
</script>

<style>
	h2 {
		cursor: pointer;
	}
</style>

<h2 on:click={() => feedOpen = !feedOpen}>
	{feed.title}
</h2>

{#if feedOpen}
	{#await articlesPromise}
		<p>Loading articles&hellip;</p>
	{:then summarizedFeed}
		{#each summarizedFeed.item as article}
			<ArticlePreview article={article} onClick={() => toggleArticle(article)} dim={$queue.has(article)} />
		{/each}
	{/await}
{/if}
