<script>
	import Queue from './Queue.svelte';
	import Reader from './Reader.svelte';
	import { queue } from './stores';
	import { onDestroy, onMount } from 'svelte';

	let darkMode = false;
	$: if (darkMode) {
		document.body.className = "dark-mode";
	} else {
		document.body.className = "";
	}

	const AppStates = {
		queueMode: 1,
		readingMode: 2,
	};

	let appState = AppStates.queueMode;

	const startReading = () => {
		appState = AppStates.readingMode;
		window.history.pushState({ appState }, '', 'read');
	}

	const goToQueue = () => {
		appState = AppStates.queueMode;
		window.history.pushState({ appState }, '', 'queue');
	}

	const popState = (event) => {
		appState = event.state.appState;
	}

	onMount(() => {
		window.addEventListener('popstate', popState);
		window.history.replaceState({ appState }, '', 'queue');
	});

	onDestroy(() => {
		window.removeEventListener('popstate', popState);
	})
</script>

<div class="app-container">
	<section class="mw7 center avenir ph3 ph0-l">
		<h2 class="baskerville fw1" on:click={goToQueue}>
			Bulletin
			<span on:click={() => darkMode = !darkMode}>🌙</span>
		</h2>

		{#if appState === AppStates.queueMode}
			<Queue
				startReading={startReading}
			/>
		{:else if appState === AppStates.readingMode}
			<Reader queue={$queue} />
		{/if}
	</section>
</div>

