import xmlParser from 'fast-xml-parser';
import he from 'he';
import Mercury from "@postlight/mercury-parser";
import { Cache } from './cache';

const storyCache = new Cache('bulletin:stories');

export async function fetchFeed(url) {
	var options = {
    attributeNamePrefix : "@_",
    attrNodeName: "attr", //default is 'false'
    textNodeName : "#text",
    ignoreAttributes : true,
    ignoreNameSpace : false,
    allowBooleanAttributes : false,
    parseNodeValue : true,
    parseAttributeValue : false,
    trimValues: true,
    cdataTagName: "__cdata", //default is 'false'
    cdataPositionChar: "\\c",
    localeRange: "", //To support non english character in tag/attribute values.
    parseTrueNumberOnly: false,
    attrValueProcessor: a => he.decode(a, {isAttributeValue: true}),//default is a=>a
    tagValueProcessor : a => he.decode(a) //default is a=>a
};

	try {
		const resp = await getFeed(url);
		const xmlRaw = await resp.text();
		const x = xmlParser.getTraversalObj(xmlRaw, options);
		const obj =  xmlParser.convertToJson(x, options);
		return cleanData(obj.rss.channel);
	} catch (e) {
		console.error(e);
		return {
			title: `Could not fetch: ${url}`,
			item: [
				{ title: e.toString() }
			]
		}
	}
}

async function getFeed(url) {
	// If fetch fails for CORS trying using proxy service.
	try {
		return await fetch(url);
	} catch (e) {
		return await fetch(`https://cors-anywhere.herokuapp.com/${url}`);
	}
}

async function cleanData(channel) {
	channel.item = channel.item.slice(0, 10);
	for (const item of channel.item) {
		for (const key in item) {
			if (typeof item[key] === 'object' && item[key].__cdata) {
				item[key] = item[key].__cdata;
			}
		}
	}

	return channel;
}

export async function loadSummaries(feed) {
	for (const item of feed.item) {
		try {
			item.summary = await getSummary(item.link);
		} catch (e) {
			// ignore
		}
	}

	return feed;
}

async function getSummary(url) {
	if (storyCache.get(url)) {
		return storyCache.get(url);
	}

	const summary = await Mercury.parse(`https://cors-anywhere.herokuapp.com/${url}`);
	summary.readingTime = getReadingTime(summary)

	storyCache.set(url, summary);
	return summary;
}

function getReadingTime(summary) {
	return Math.max(1, Math.round(summary.word_count / 200));
}
