import { writable } from 'svelte/store';

export const queue = writable(new Set());

const FEED_URLS_KEY = `bulletin:feedUrls`;

const initialFeedUrls = function() {
	try {
		const stored = localStorage.getItem(FEED_URLS_KEY);
		return stored === null ? [] : JSON.parse(stored);
	} catch (e) {
		return []
	}
}();

export const feedUrls = writable(new Set(initialFeedUrls));

feedUrls.subscribe((urls) => {
	localStorage.setItem(FEED_URLS_KEY, JSON.stringify([...urls.values()]))
});
