<script>
	export let queue;

	import { afterUpdate } from 'svelte';

	let articleIdx = 0;
	$: article = [...queue.values()][articleIdx];

	afterUpdate(() => {
		window.scrollTo(0, 0);
	})

</script>

<h1>
	{article.title}
</h1>

{#if article.summary}
	{#if article.summary.readingTime}
		<p>
			{article.summary.readingTime} minute read
		</p>
	{/if}

	<img src={article.summary.lead_image_url} />
{/if}

<div class="baskerville f4 lh-copy">
	{@html article.summary.content}
</div>

<button on:click={() => articleIdx--} disabled={articleIdx === 0}>Previous Article</button>
<button on:click={() => articleIdx++} disabled={articleIdx === queue.size - 1}>Next Article</button>
