export class Cache {
	constructor(cacheKey) {
		this._cacheKey;
		this._load();
	}

	get(key) {
		return this._data[key];
	}

	set(key, value) {
		this._data[key] = value;
		this._write();
	}

	_write() {
		window.sessionStorage.setItem(this._cacheKey, JSON.stringify(this._data));
	}

	_load() {
		const str = window.sessionStorage.getItem(this._cacheKey);
		this._data = str ? JSON.parse(str) : {};
	}
}