<script>
	import Feed from './Feed.svelte';
	import FeedForm from './FeedForm.svelte';
	import { fetchFeed } from './rss_fetch';
	import { queue, feedUrls } from './stores';

	$: fetchFeeds = Promise.all([...$feedUrls.values()].map(url => fetchFeed(url)));

	export let startReading;

	$: queueTime = [...$queue.values()].reduce((time, i) => time + (i.summary ? i.summary.readingTime : 0), 0);
	let readingSessionDuration = 15;

	const toggleStory = (item) => {
		queue.update(q => {
			if (q.has(item)) {
				q.delete(item);
				return q;
			} else {
				return q.add(item);
			}
		})
		
	}

	function addFeed(newUrl) {
		feedUrls.update(urls => {
			return new Set([...urls.values(), newUrl]);
		})
	}
</script>

<h1>Reading Queue</h1>

<div>
	Reading Session: <input type=range bind:value={readingSessionDuration} min=0 max=60> {readingSessionDuration} minutes
</div>

<ul>
{#each [...$queue.values()] as item}
	<li>
		{item.title}
		<button on:click={() => toggleStory(item)}>Remove</button>
		{#if item.summary}
			{item.summary.readingTime} minute read
		{/if}
	</li>
{/each}
</ul>

Remaining: {readingSessionDuration - queueTime} minutes

<button on:click={startReading} disabled={$queue.size === 0}>Start Reading</button>

{#await fetchFeeds}
	<p>Loading feeds&hellip;</p>
{:then feeds}
	{#each feeds as feed}
		<Feed feed={feed} toggleArticle={toggleStory} />
	{/each}
{/await}

<FeedForm onAddFeedUrl={addFeed} />
